<template>
  <div>
    <div class="block">
      <el-timeline v-for="item in operation" :key="item.operateTime">
        <el-timeline-item
          :timestamp="item.operateTime"
          placement="top"
          icon="el-icon-star-on"
          color="#67c23a"
        >
          <el-card>
            <h4>{{ item.operate }}</h4>
          </el-card>
        </el-timeline-item>
      </el-timeline>
    </div>
  </div>
</template>

<script>
export default {
  name: "Operate",

  data() {
    return {
      operation: [
        {
          operate: "操作",
          operateTime: "2022-4-24 17:18:00",
        },
      ],
    };
  },

  mounted() {},

  created() {
    this.getOperation();
  },

  methods: {
    getOperation() {
      this.$axios
        .get("/admin/getOperation")
        .then((res) => {
          this.operation = res.data.data;
          // 时间转换
          this.operation.forEach((item) => {
            item.operateTime = this.$moment(item.operateTime).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          });
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
  },
};
</script>

<style lang="less" scoped>
/deep/.el-table th {
  height: 60px;
  line-height: 60px;
}
/deep/.el-pagination {
  height: 16px;
  line-height: 16px;
}
h4,
p {
  height: 40px;
  line-height: 40px;
}
</style>